import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
			num: null,
            list: [],
			Patient: this.$store.state.patient,
            user: this.$store.state.userInfo.Data,
            pageShow: false,
			timeShow: false,
			show: false, 
			navList : [],
			disGroupArr: [],
			info:[],
			a: '0%',
			disGroupId: 0,
			patientName:'',
			picturePath:'',
			format: 'YYYY-MM-DD',
			expandTimeValue: '',
			value:'',
			textareaVal:'',
			drInfos:null,
			selectDr:null,
			referralRemark:'',
			primaryDr: null, //基层医生
        }
    },
    created(){
		this.remark(this.Patient.PatientId);
		//console.log(this.num)
    },
    mounted(){
		
    },
    beforeDestroy(){
		
    },
    methods:{
		disGroupGet(idx){
			this.num = idx;
			this.pullDr(this.Patient.PatientId);
		},
		remark(patientId){
		    this.$get('PlatFormAPI/DiseaseGroupAndRule/QueryPatientDisGroup',{
		        PatientId: patientId,
		    }).then(o => {
				if(o && o.length){
					this.num = o[0]
					this.pullDr(patientId)
				}
				this.navList = o || []
				const nLength = parseInt(100 / this.navList.length) +'%';
				this.a = nLength;
		    })
		},
		pullDr(patientId){
			let primaryDr = null
			this.$get('PlatFormAPI/DoctorReferral/QueryPatientDocReferralSet',{
			    PatientId: patientId,
				DisGroupId:this.num.DisGroupId
			}).then(o => {
				this.info = o;
				this.disGroupArr = o.DrInfos;
				this.patientName = o.PatientName;
				this.picturePath = o.PicturePath;
				if(this.info && this.info.DrInfos){
					this.drInfos = []
					this.drInfos.length = 5
					this.info.DrInfos.forEach((v,idx) => {
						let n = ""
						this.drInfos[v.ProfessionalLevel - 1] = v
						switch(v.ProfessionalLevel){
					        case 1: 
					            n = '基层管理医生'
					        break;    
					        case 2: 
					            n = '乡村管理医生'
					        break;    
					        case 3: 
					            n = '区级管理医生'
					        break;
					        case 4: 
					            n = '市级管理医生'
					        break;
					        case 5: 
					            n = '省级管理医生'
					        break;
					        default:
					        break; 
					   }
						this.drInfos[v.ProfessionalLevel - 1].ProfessionalName = n
						if(v.ProfessionalLevel == 1){
							primaryDr = v.DrId 
							this.primaryDr = v.DrId
						}
					})
				}
				
			})
		},
		
		initiationReferral(i){
			this.selectDr = i;
			this.show = true;
		},
		cancel(){
			this.show = false;
		},
		cancel2(){
			this.timeShow = false;
		},
		timeDialog(){
			this.timeShow = true;
			let now = new Date();
			let cmonth = now.getMonth() + 1;
			let day = now.getDate();
			if (cmonth < 10) cmonth = '0' + cmonth;
			if (day < 10) day = '0' + day;
			this.value = now.getFullYear() + '-' + cmonth + '-' + day;
		},
		exchangeConfirm () {
			this.timeShow = false
			if(!this.expandTimeValue){
				this.expandTimeValue = this.value
			}
			document.getElementById("time").value = this.expandTimeValue;
			//console.log(this.expandTimeValue);
		},
		timeConfirm(){
			let param = this.num;
			console.log(param)
			this.textareaVal = this.$refs.textareaVal.value;
			console.log(this.drInfos)
			this.drInfos.forEach((n, idx) => {
				if(n.ProfessionalLevel) {
					switch(n.ProfessionalLevel) {
						case 1:
							//'基层管理医生'
							param.BasicDrId = n.DrId || null
							param.BasicDrName = n.DrName || null
							break;
						case 2:
							//'乡村管理医生'
							param.CountryDrId = n.DrId || null
							param.CountryDrName = n.DrName || null
							break;
						case 3:
							//'区级管理医生'
							param.AreaDrId = n.DrId || null
							param.AreaDrName = n.DrName || null
							break;
						case 4:
							//'市级管理医生'
							param.CityDrId = n.DrId || null
							param.CityDrName = n.DrName || null
							break;
						case 5:
							//'省级管理医生'
							param.ProvinceDrId = n.DrId || null
							param.ProvinceDrName = n.DrName || null
							break;
						default:
							break;
					}
				}
			})
			param.FromDrId = this.user.User.DoctorId
			param.FromDrName = this.user.User.DoctorName
			param.ToDrId = this.selectDr.DrId
			param.ToDrName = this.selectDr.DrName
			param.ReferralRemark = this.referralRemark
			param.PatientId = this.info.PatientId
			param.PatientName = this.info.PatientName
			param.ReferralTime = this.expandTimeValue
				
			//确定转诊
			this.$post('/PlatFormAPI/DoctorReferral/SavePatientReferralSet',param).then(o => {
				this.$toast('提交成功');
				this.show = false;
			})
			//console.log(this.textareaVal);
			//console.log(this.expandTimeValue);
			//console.log(this.DisGroupId);
		},
		goDrList(i) {
			if( this.user.User.ProfessionalLevel != 1){
				//该转诊团队的基层医生不可设置 登录用户为非基层医生也不能设置
				return false;
			}
			if(i.ProfessionalLevel == 1){
				//该转诊团队的基层医生不可设置 登录用户为非基层医生也不能设置
				return false;
			}
			this.$store.dispatch('pushProDrInfos', this.drInfos)
			this.$router.push({
				path: "/doctor-list",
				query: {
					id: this.primaryDr,
					level: i.ProfessionalLevel,
					DisGroupId: this.num.DisGroupId,
					DisGroupName: this.num.DisGroupName
				}
			})
		}
		
    }
}



